.app {
  height: 100%;
  display: flex;
  flex-direction: column;
}

header {
  position: sticky;
  top: 0;
  padding: 1em 2em;
  display: flex;
  align-items: center;
  gap: .5em;
}

.header-logo {
  font-size: 1.2rem;
}

.theme-toggle {
  cursor: pointer;
  border: none;
  /* border: 1px solid var(--bg-color); */
  background-color: var(--color);
  color: var(--bg-color);
}

main {
  flex: 1 1 auto;
  padding-inline: 2em;
}

.card-list {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  /* display: grid;
  grid-template-columns: repeat(auto-fit, 15em);
  grid-template-rows: masonry; */
  gap: .5em;
  list-style-type: none;
  opacity: 0;
  transition: opacity .5s ease;
}

.card-links {
  display: flex;
  gap: .5em;
}

.card-link {
  display: inline-block;
  text-decoration: none;
  padding: .5em;
  border-radius: .3em;
  background-color: gainsboro;
  color: black;
}

.card-link.primary {
  background-color: blue;
  color: white;
}

footer {
  flex-shrink: 0;
  padding-block: .5em;
  display: flex;
  justify-content: center;
  font-size: .9rem;
}